.wrapper {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  gap: 0;
  width: 560px;
  margin: 200px auto;
}

.operation-result {
  background-color: grey;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: end;
      justify-content: flex-end;
  padding-right: 6px;
  -ms-flex-align: center;
      align-items: center;
  height: 70px;
  color: white;
  font-size: 1.4rem;
}

.main-section {
  display: grid;
  grid-template-columns: 140px 140px 140px 140px;
  grid-template-rows: 70px 70px 70px 70px 70px;
  grid-template-areas:
    "a b c d"
    "e f g h"
    "i j k l"
    "m n o p"
    "q q r s";
}

